@font-face {
    font-family: 'Goldman';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/goldman/v1/pe0uMIWbN4JFplR2HDVyBw.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
  }

html {
    box-sizing: border-box;
}
  
body {
    height: 100vh;
    font-size: 1.4rem;
    margin: 0;
    font-family: "Goldman";
    font-size: 1.2rem;
}

#root {
    height: 100%;
}

.mainPage {
    background-color:rgba(0, 0, 0, 0.199);
    display: grid;
    grid-template-areas: "HeaderSection" "sectionArea1" "ContactSection";
    grid-template-columns: 1fr;
    grid-template-rows: 150px 100rem 150px;
    height: 100%;
}

.SidePage {
    display: grid;
    grid-template-areas: "HeaderSection" "MainSection";
    grid-template-columns: 1fr;
    grid-template-rows: 150px;
    height: 100%;
}


/* ************* */
/* MAIN PAGE */
/* ************* */


.MainSection {
    background-image: url("./images/plates2.jpg");
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100%;
}

.HeaderSection {
    background-color: rgba(255, 255, 255, 0.938);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 5px;
}

.LoginSection {
    background-color: rgba(255, 255, 255, 0.726);
    border-radius: 5px;
    height: 20%;
    width: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.LoginSection div {
    height: 70%;
}

.InfoSection {
    background-color: rgba(255, 255, 255, 0.726);
    border-radius: 5px;
    height: 30%;
    width: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.InfoSection h2 {
    margin: 0px;
}

.ContactSection {
    background-color: rgb(53, 53, 53);
    display: flex;
    align-items: center;
    justify-content: center;
}

.sectionArea div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sectionArea1 {
    background-image: url("./images/plates.jpg");
    background-position: right;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.headerRight {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    padding: 10px;
}

.headerRight h4 {
    margin: 0;
}

.headerRightLinks {
    display: flex;
    flex-direction: column;
}

.headerRightLinks div {
    margin: 10px;
}

.headerLeft {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
}

.headerLeft h1 {
    margin: 0px;
    font-style: italic;
    font-size: 2.5rem;
}

.headerLeft h3 {
    margin: 0px;
    font-style: italic;
    font-size: 1.5rem;
}

.logo {
    height: 130px;
}

.logoMini {
    height: 60px;
}

.promoImage {
    display: flex;
    width: 90%;
}

.mainNav {
    margin: 2px;
    width: 120px;
    height: 60px;
    border-radius: 5px;
    background-color:rgb(17, 88, 146);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    transition: background-color, 1s;
}

.mainNav:hover {
    background-color:rgb(7, 65, 112);
}

.mainNavActive {
    border-style: solid;
    border-color: rgb(130, 204, 247);
    width: 200px;
    height: 80px;
    border-radius: 5px;
    background-color:rgb(0, 50, 92);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}


.ContactSection {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white
}

.ContactSection h3 {
    margin: 0px;
}

.ContactSection a {
    text-decoration: none;
    color: white;
    margin: 5px;
}

/* ************* */
/* ************* */



/* ************* */
/* USER PAGE */
/* ************* */



.userPage {
    display: grid;
    grid-template-areas: "HeaderSection" "Navigation" "MainSection";
    grid-template-columns: 1fr;
    grid-template-rows: 50px 100px;
    height: 100%;
}

.Navigation {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color:rgb(17, 88, 146);
}

.nav {
    font-family: "Goldman";
    font-size: 1.2rem;
    border-style: solid;
    border-color: rgb(130, 204, 247);
    width: 200px;
    height: 75px;
    border-radius: 5px;
    background-color:rgb(17, 88, 146);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    transition: background-color, 0.5s;
    margin-bottom:2px;
}

.nav:hover {
    background-color:rgb(7, 65, 112);
}

.navActive {
    font-family: "Goldman";
    font-size: 1.2rem;
    border-style: solid;
    border-color: rgb(130, 204, 247);
    width: 200px;
    height: 75px;
    border-radius: 5px;
    background-color:rgb(0, 50, 92);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    margin-bottom:2px;
}

.userHeaderRight {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    padding: 10px;
}

.userHeaderRight h3 {
    margin-right: 15px;
    border-radius: 5px;
    background-color:rgba(255, 255, 255, 0.349);
    padding: 6px;
}

#logoutButton {
    height: 35px;
    width: 100px;
}


/* ************* */
/* ************* */


.userPageMain {
    display: grid;
    grid-template-areas: "dayNavigation" "daySection";
    grid-template-columns: 220px 1fr;
    grid-template-rows: 1fr;
    width: 99%;
    padding-top: 8px;
}

.dayNavigation {
    padding-left: 10px;
}

.daySection {
    display: flex;
    align-items: top;
    justify-content: left;
}

.weekDay {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

.weekDay table {
    border-radius: 5px;
}

.weekDay table th {
    min-width: 120px;
    background-color: rgb(81, 160, 187);
    border-radius: 5px;
}

.weekDay table th:nth-child(2) {
    min-width: 250px;
}

.weekDay table th:nth-child(1) {
    min-width: 90px;
}

.weekDay table th:nth-child(1) button {
    width: 30px;
}

.weekDay table th:nth-child(5) button {
    width: 300px;
    height: 40px;
}

.weekDay table td {
    background-color: white;
    border-radius: 5px;
    text-align: center;
}

.routine {
    height: 100%;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
}

.excerciseForm {
    padding: 5px;
    margin: 10px;
    display: flex;
    align-items: left;
    justify-content: left;
    flex-direction: column;
    border-radius: 5px;
    background-color:rgb(210, 210, 235);
}

.excerciseForm div input {
    margin-right: 10px;
}

.excerciseForm h3 {
    margin: 0px;
}

.excerciseForm button {
    width: 100px;
    height: 30px;
    border-radius: 5px;
    font-family: "Goldman";
    font-size: 1.2rem;
    margin: 3px;
}

#noWork {
    text-align: center;
}




/* ************* */
/* ************* */

.userPageStats {
    display: grid;
    grid-template-areas: "weekNavigation" "exerciseList";
    grid-template-rows: 100px 1fr;
    grid-template-columns: 1fr;
    width: 100%;
    height: 100%;
    align-items: top;
    width: 99%;
    margin-top: 5px;
    padding-top: 8px;
}

.userPageStats div {
    display: flex;
    justify-content: center;
}

.weekNavigation {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.weekNavigationSub {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.weekNav {
    width: 110px;
    height: 30px;
    border-radius: 5px;
    font-family: "Goldman";
    font-size: 1.2rem;
    margin: 3px;
    cursor: pointer;
}

.weekNav:hover {
    background-color:rgb(183, 197, 206);
}


.exerciseList table {
    border-radius: 5px;
}

.exerciseList table th {
    min-width: 120px;
    background-color: rgb(81, 160, 187);
    border-radius: 5px;
}

.exerciseList table th:nth-child(2) {
    min-width: 250px;
}

.exerciseList table td {
    border-radius: 5px;
    text-align: center;
}

td button {
    height: 30px;
    border-radius: 5px;
    font-family: "Goldman";
    font-size: 1.2rem;
    margin: 3px;
    cursor: pointer;
    background-color:rgb(232, 239, 243);
    transition: background-color, 0.2s;
}

td button:hover {
    background-color:rgb(183, 197, 206);
}

.excerciseCompleted {
    background-color: rgb(209, 255, 101);
}

.excerciseIncomplete {
    background-color: rgb(255, 255, 255);
}

/* ************* */
/* ************* */

.userDetailsMain{
    border-radius: 5px;
    background-color:rgba(255, 255, 255, 0.712);
    margin: 30px;
}

.userDetails {
    padding: 5px;
    margin: 10px;
    display: flex;
    align-items: left;
    justify-content: left;
    flex-direction: column;
    
}

.userDetails h3 {
    margin: 0px 0px 20px 20px;
}

.userDetails h4 {
    margin: 0px;
}

#goalForm {
    margin: 0px 0px 20px 20px;
}

#goalForm input {
    width: 30px;
    margin: 10px;
}

#goalForm button {
    font-size: 1.2rem;
    width: 100px;
    height: 30px;
    margin: 10px;
}



/* ************* */
/* ************* */


/* ************* */
/* RETURN BUTTON */
/* ************* */

.return {
    border-style: solid;
    border-color: rgb(226, 224, 247);
    width: 120px;
    height: 40px;
    border-radius: 5px;
    background-color:rgb(116, 141, 253);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    transition: background-color, 0.5s;
}

.return:hover {
    background-color:rgb(61, 87, 235);
}

.registerLoginButtons {
    margin-top: 10px;
    border-style: solid;
    border-color: rgb(44, 43, 51);
    width: 11rem;
    height: 35px;
    font-size: 1.3rem;
    border-radius: 5px;
    background-color:rgb(116, 141, 253);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    transition: background-color, 0.5s;
}

.registerLoginButtons:hover {
    background-color:rgb(61, 87, 235);
}

.registerLoginButtons a {
    text-decoration: none;
    color: white;
}

/* ************* */
/* ************* */




/* ************* */
/* FORMS */
/* ************* */

.formContainer{
    margin: 30px;
}

.formContainer form {
    background-color: rgba(255, 255, 255, 0.801);
    padding: 40px 0px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 20rem;
    height: 25rem;
    font-family: "Goldman";
    font-size: 1.2rem;
}


.formInput {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formInput input {
    margin: 10px;
    width: 120%;
}

form button {
    width: 200px;
    height: 60px;
    border-radius: 5px;
    font-family: "Goldman";
    font-size: 1.8rem;
    cursor: pointer;
    background-color:rgb(255, 255, 255);
    transition: background-color, 0.2s;
}

form button:hover {
    background-color:rgb(130, 197, 252);
}


/* ************* */
/* MISC */
/* ************* */

.toProfile {
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.301);
    width: 220px;
    height: 40px;
    border-radius: 5px;
    background-color:rgba(67, 47, 243, 0.589);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    transition: background-color, 1s;
}

.toProfile:hover {
    background-color:rgb(30, 59, 221);
}

.LogoutSection {
    background-color: rgba(255, 255, 255, 0.726);
    border-radius: 5px;
    width: 30rem;
    height: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.registeredSection {
    background-color: rgba(255, 255, 255, 0.726);
    border-radius: 5px;
    width: 30rem;
    height: 15rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
}

.registeredSection h4 {
    margin: 0px;
}